.categories-card-container{display:grid;gap:50px}@media screen and (min-width: 1920px){.categories-card-container{grid-template-columns:repeat(4, 1fr)}}@media(min-width: 1366px)and (max-width: 1920px){.categories-card-container{grid-template-columns:repeat(3, 1fr)}}@media(min-width: 768px)and (max-width: 1366px){.categories-card-container{grid-template-columns:repeat(2, 1fr)}}@media(min-width: 480px)and (max-width: 768px){.categories-card-container{grid-template-columns:repeat(1fr)}}@media(max-width: 480px){.categories-card-container{grid-template-columns:1fr}}